<template>
  <chart class="ChartsPie" :options="option" ref="ChartsPie"/>
</template>

<script>
import ECharts from "vue-echarts/components/ECharts";
import "echarts/lib/chart/line";
import "echarts/lib/chart/pie";
import "echarts/lib/component/polar";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import "echarts/lib/component/dataZoom";

export default {
  name: "ChartsPie",
  mounted() {},
  data() {
    return {
      option: {
        color: ["#24A9F0", "#D2D2D2", "#4FDAD2", "#F3D38C", "#C9F0F4"],
        title: {
          text: "",
          textStyle: {
            fontSize: 12,
            aling: "center",
            color: "#777777",
            fontWeight:300
          },
          left: "center"
        },
        tooltip: {
          show:false
        },
        legend: {
          orient: "horizontal",
          selectedMode:false,
          top: "bottom",
          right: "center",
          data: [],
          padding:0,
          itemGap:2,
          itemWidth:15,
          itemHeight:7,
          textStyle:{
            color: "#777777",
            fontSize: 10
          },
          formatter: params => {
            for (let i = 0; i < this.option.series[0].data.length; i++) {
              if (this.option.series[0].data[i].name == params) {
                return (
                  params + ":" + this.option.series[0].data[i].value + ""
                );
              }
            }
          }
        },
        series: [
          {
            type: "pie",
            radius: ["35%", "55%"],
            avoidLabelOverlap: false,
            silent:true,
            label:{
              normal:{
                show:true,
                position: 'center',
                color:'#777777',
                fontSize:10,
                formatter:(params) => {
                  if(params.data.name == ''){
                    return `\n\n`+params.percent+`%`
                  }else{
                    return ''
                  }
                }
              },
              emphasis: {
                    show: false
                }
            },
            data: []
          }
        ]
      }
    };
  },
  props: {
    data: {
      type: Object
    }
  },
  mounted() {
    this.option.title.text = this.data.goalDetail;
    this.data.condation.map(item => {
      this.option.legend.data.push(item.name);
      this.option.series[0].data.push({ value: item.value, name: item.name });
    });
  },
  watch: {
    data(newVal) {
      if (newVal) {
        this.option.legend.data = [];
        this.option.series[0].data = [];
        for (let item of newVal) {
          console.log(item);
        }
        // newVal.map(item =>{
        //   this.option.legend.data.push(item.progress)
        //   this.option.series[0].data.push({value:item.count,name:item.progress})
        // })
      } else {
        this.option.legend.data = [];
        this.option.series[0].data = [];
      }
    }
  },
  components: { chart: ECharts },
  methods: {
    selfAdaption() {
      let _this = this;
      setTimeout(() => {
        window.addEventListener("resize", function() {
          _this.$refs.chartsAll.resize();
        });
      }, 10);
    }
  }
};
</script>

<style scoped>
.ChartsPie {
  height: 100%;
  width: 100%;
}
</style>



